<template>
    <div class="wrap">
        <div class="col-con">
            <div class="request">
                <h2>Request</h2>
                <div class="item">
                    <span class="label">code </span>
                    <Select v-model="code">
                        <Option v-for="item in codeList" :value="item.value" :key="item.value">{{item.label}}</Option>
                    </Select>
                </div>
                <div class="item">
                    <span class="label">robot</span>
                    <Select v-model="robot">
                        <Option v-for="item in robotList" :value="item.value" :key="item.value">{{item.label}}</Option>
                    </Select>
                </div>
                <div class="item">
                    <span class="label">data </span>
                    <i-input type="textarea" :rows="7" placeholder='{"key" : value }' v-model="dataCon"></i-input>
                </div>
                <div class="item" style="text-align: right;"><i-button type="primary" @click="submit"></i-button></div>
            </div>
            <div class="response">
                <h2>Response</h2>
                <span v-if="load">Loading...</span>
                <div class="response-body">{{responseCon}}</div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'name',
    data: function() {	
        return {
            ws:new WebSocket(`ws://${sessionStorage.controlIP}:7200/test/ws/0120508cd011000120808031086a09ab50212040807100012`),
            // ws:new WebSocket(this.baseJs.wsUrl),
            load: false,
            robot: '',
            robotList: [],
            code: null,
            codeList: [
                // {label:'-(100)-Code_HandShake',value: 100},
                // {label:'-(101)-Code_HeartBeat',value: 101},
                {label:'-(102)-Code_Robots',value: 102},
                {label:'-(103)-Code_WxStatus',value: 103},
                {label:'-(104)-Code_Reset',value: 104},
                {label:'-(105)-Code_QrcodeLogin',value: 105},
                // {label:'-(106)-Code_QrcodeImgBuf',value: 106},
                // {label:'-(107)-Code_QrcodeStatus',value: 107},
                {label:'-(108)-Code_UsrPwdLogin',value: 108},
                // {label:'-(109)-Code_PhoneLogin',value: 109},
                {label:'-(110)-Code_SecondAuthLogin',value: 110},
                // {label:'-(111)-Code_InitWeixinData',value: 111},
                {label:'-(112)-Code_UsernameLists',value: 112},
                // {label:'-(113)-Code_AsyncMessage',value: 113},
                {label:'-(114)-Code_Contacts',value: 114},
                {label:'-(115)-Code_SendMsg',value: 115},
                {label:'-(116)-Code_SendMsgImage',value: 116},
                {label:'-(117)-Code_SendCardMsg',value: 117},
                {label:'-(118)-Code_SendVoiceMsg',value: 118},
                {label:'-(119)-Code_MessSendMsg',value: 119},
                {label:'-(120)-Code_MessSendVoice',value: 120},
                {label:'-(121)-Code_MessSendImage',value: 121},
                {label:'APP-(122)-Code_SendAppMsg',value: 122},
                {label:'-(123)-Code_DownloadImage',value: 123},
                {label:'-(124)-Code_DownloadVoice',value: 124},
                // {label:'-(125)-Code_CreatHongBao',value: 125},
                // {label:'-(126)-Code_TenPayRequest ',value: 126},
                {label:'-(127)-Code_OptionLog',value: 127},
                {label:'-(128)-Code_SearchContact',value: 128},
                {label:'-(129)-Code_GetUserByWxid',value: 129},
                {label:'-(130)-Code_UserOptionLog',value: 130},
                {label:'-(131)-Code_ChangePassword',value: 131},
                {label:'-(132)-Code_CancelUser',value: 132},
                {label:'-(133)-Code_UploadHeadImg',value: 133},
                {label:'-(133)-Code_SetPersonInfo',value: 134},
                {label:'-(135)-Code_SetAddFrom',value: 135},
                {label:'-(136)-Code_SetWeixinUsername',value: 136},
                {label:'-(137)-Code_BindMobile',value: 137},
                {label:'-(138)-Code_BindEail',value: 138},
                {label:'-(139)-Code_CreateChatRoom',value: 139},
                {label:'-(140)-Code_ChangeChatRoomName',value: 140},
                {label:'-(141)-Code_InviteChatRoom',value: 141},
                {label:'-(142)-Code_ExitChatRoom',value: 142},
                {label:'-(143)-Code_DelChatRoomUser',value: 143},
                {label:'-(144)-Code_GetRoomQrcode',value: 144},
                {label:'-(145)-Code_GetRoomDetail',value: 145},
                {label:'-(146)-Code_AddChatRoomMember',value: 146},
                {label:'-(147)-Code_ChatRoomVerifyOnOff',value: 147},
                {label:'-(148)-Code_ApproveAddChatroomMember',value: 148},
                {label:'-(149)-Code_SetChatRoomAnnouncement',value: 149},
                {label:'-(150)-Code_NearPeople',value: 150},
                {label:'-(151)-Code_Yao1Yao',value: 151},
                {label:'-(152)-Code_SnsPost',value: 152},
                {label:'-(153)-Code_SnsTimeLine',value: 153},
                {label:'-(154)-Code_SnsObjectOp',value: 154},
                {label:'-(155)-Code_SnsUserPage',value: 155},
                {label:'-(156)-Code_SnsUploadImg',value: 156},
                {label:'-(157)-Code_SnsComment',value: 157},
                {label:'-(158)-Code_GetA8Key',value: 158},
                // {label:'()-(200)-Controller_LogMsg',value: 200},
                {label:'()-(201)-Controller_Version',value: 201},
                {label:'()-(202)-Controller_SetupRobot',value: 202},
                {label:'()-(203)-Controller_UpdateRobot',value: 203},
                {label:'()-(204)-Controller_DeleteRobot',value: 204},
            ],
            dataCon: '',
            responseCon: '',
        }
    },
    created: function(){
        var _this = this;
        _this.ws.onopen = function () {};
        _this.ws.onmessage = function (result) {
			var data = JSON.parse(result.data);
			// 
			if(data.code == 102){
				var tempList = [];
				data.data.forEach(function(item,index){
					tempList.push({
						value : item.exdata.robotId,
						label : item.exdata.robotName
					});
				})
				_this.robotList = tempList;
			};
			if(data.code == _this.code){
				_this.load = false;
				_this.responseCon = data;
			}
		}
    },
    methods: {
        // 
        submit: function(){
            var _this = this;
            _this.load = true;
            _this.responseCon = '';
            var sendData = JSON.stringify({
                code: _this.code,
                robotId: _this.robot,
                data: _this.dataCon?JSON.parse(_this.dataCon):''
            });
            _this.ws.send(sendData);
        },
    },
    beforeDestroy: function(){
        this.ws.close();
    }
}
</script>

<style scoped>
    .wrap{height: 100%;}
    .ivu-col{padding-top: 10px;}
    .ivu-col:first-child{padding-right: 10px;}
    .col-con{border: 1px solid rgb(221, 222, 225);padding: 20px;}
    .item{margin-bottom: 20px;}
    .label{font-size: 16px;font-weight: bold;}
    .response{border-top: 2px solid #ccc;margin-top: 20px;padding-top: 20px;}
    .response-body{height: 100%;height: 200px;border: 1px solid #ccc;overflow: auto;background:#f7f7f9;border-radius: 10px;padding: 10px;word-wrap:break-word;}
</style>


