<template>
    <div>
        <Spin fix v-if="loading"></Spin>
        <div class="table-box">
            <Table :columns="serverCol" :data="gameList"></Table>
        </div>
        <div class="table-footer">
            <Row>
                <Col span="24">
                    <Button type="primary" @click="openAeServerForm"></Button>
                </Col>
            </Row>
        </div>
        <!--S -->
		<Modal v-model="aeServerProp.show" :title="aeServerProp.isEdit?'':''" :width="400" :mask-closable="false">
			<Form ref="aeServerForm" :model="aeServerProp.propObj" :rules="aeServerRule" :label-width="100">
				<FormItem prop="name" label="">
					<Input type="text" v-model="aeServerProp.propObj.name" placeholder="..."></Input>
				</FormItem>
				<FormItem prop="ip" label="IP">
					<Input type="text" v-model="aeServerProp.propObj.ip" placeholder="..."></Input>
				</FormItem>
				<FormItem prop="rootUsr" label="">
					<Input type="text" v-model="aeServerProp.propObj.rootUsr" placeholder="..."></Input>
				</FormItem>
				<FormItem prop="rootPwd" label="">
					<Input type="password" v-model="aeServerProp.propObj.rootPwd" placeholder="..."></Input>
				</FormItem>
				<FormItem prop="privateKey" label="">
					<Input type="textarea" v-model="aeServerProp.propObj.privateKey" placeholder="..."></Input>
				</FormItem>
				<FormItem prop="area" label="">
					<Input type="text" v-model="aeServerProp.propObj.area" placeholder="..."></Input>
				</FormItem>
				<FormItem prop="type" label="">
					<RadioGroup v-model="aeServerProp.propObj.type">
                        <Radio label="robot"></Radio>
                        <Radio label="controller"></Radio>
                        <Radio label="gameapi">API</Radio>
                    </RadioGroup>
				</FormItem>
			</Form>
			<div slot="footer">
				<Button type="primary" @click="smAeServer" :loading="aeServerProp.btnLoad"></Button>
			</div>
		</Modal>
		<!--E -->
    </div>
</template>

<script>
export default {
    name: 'name',
    data: function() {	
        return {
            opID: null,
            loading: false,
            serverCol: [
                {title: 'ID',key: '_id'},
                {title: '',key: 'name'},
                {title: '',key: 'area'},
                {title: 'IP',key: 'ip'},
                {
                    title: '',key: 'op',width: 300,align: 'center',
                    render: (h, params) => {
                        var _this = this;
                        return h('div', [
                            h('Button',{
                                props: {
                                    type: 'ghost',
                                    size: 'small',
                                },
                                style: {
                                    marginRight: '5px'
                                },
                                on:{
                                    click: function(){
                                        // _this.installController(params.row._id);
                                    }
                                }
                            },'API'),
                            h('Button',{
                                props: {
                                    type: 'ghost',
                                    size: 'small',
                                },
                                style: {
                                    marginRight: '5px'
                                },
                                on:{
                                    click: function(){
                                        _this.opID = params.row._id;
                                        _this.openAeServerForm('edit',params.row);
                                    }
                                }
                            },''),
                            h('Button',{
                                props: {
                                    type: 'error',
                                    size: 'small',
                                },
                                on:{
                                    click: function(){
                                        _this.opID = params.row._id;
                                        _this.$Modal.confirm({
                                            title: '',
                                            content: '<p></p>',
                                            loading: true,
                                            onOk: () => {
                                                _this.delServer(_this.opID);
                                            }
                                        });
                                    }
                                }
                            },'')
                        ]);
                    }
                }
            ],
            aeServerProp: {
                show: false,
                btnLoad: false,
                isEdit: false,
                propObj: {
                    type: '',
                    ip: '',
                    name: '',
                    area: '',
                    rootUsr: '',
                    rootPwd: '',
                    privateKey: '',
                    installed: false
                }
            },
            aeServerRule: {
                name: this.baseJs.textRule,
                ip: this.baseJs.textRule,
                rootUsr: this.baseJs.textRule,
                area: this.baseJs.textRule,
            },
        }
    },
    computed: {
        gameList(){
            var arr = this.serverList.filter(item=>{
                return item.type == 'gameapi'
            })
            return arr;
        },
        serverList(){
            return this.$store.state.serverList;
        },
    },
    methods: {
        // /
        openAeServerForm: function(edit,obj){
            var _this = this;
            _this.$refs['aeServerForm'].resetFields();
            if(edit == 'edit'){
                this.aeServerProp.isEdit = true;
                _this.aeServerProp.propObj.name = obj.name;
                _this.aeServerProp.propObj.ip = obj.ip;
                _this.aeServerProp.propObj.rootUsr = obj.rootUsr;
                _this.aeServerProp.propObj.rootPwd = obj.rootPwd;
                _this.aeServerProp.propObj.privateKey = obj.privateKey;
                _this.aeServerProp.propObj.type = obj.type;
                _this.aeServerProp.propObj.area = obj.area;
            }else{
                this.aeServerProp.isEdit = false;
            }
            this.aeServerProp.show = true;
        },
        // /
        smAeServer: function(){
            var _this = this;
            _this.$refs['aeServerForm'].validate((valid) => {
                if (valid) {
                    _this.aeServerProp.btnLoad = true;
                    var url = '';
                    var sendData = _this.baseJs.copyObj(_this.aeServerProp.propObj);
                    if(_this.aeServerProp.isEdit){
                        sendData._id = _this.opID;
                        url = '/servers/update'
                    }else{
                        url = '/servers/create'
                    }
                    _this.axios.post(`${_this.baseJs.postUrl}${url}`,sendData).then(function(response){
                        var result = response.data;
                        if(result.result){
                            _this.aeServerProp.btnLoad = false;
                            _this.aeServerProp.show = false;
                            _this.$Message.success('');
                            _this.$store.dispatch('getServerList');
                        }else{
                            _this.aeServerProp.btnLoad = false;
                            _this.$Message.error(result.msg);
                        }
                    });
                } else {
                    _this.$Message.error('');
                }
            })
        },
        // 
        delServer: function(id){
            var _this = this;
            var sendData = {_id: id}
            _this.axios.post(`${_this.baseJs.postUrl}/servers/delete`,sendData).then(function(response){
                var result = response.data;
                if(result.result){
                    _this.$Modal.remove();
                    _this.$Message.success('');
                    _this.$store.dispatch('getServerList');
                }else{
                    _this.$Modal.remove();
                    _this.$Message.error(result.data);
                }
            });
        },
    }
}
</script>

<style scoped>
.table-box{
    position: absolute;
    top: 0;
    bottom: 70px;
    right: 0;
    left: 0;
    overflow: auto;
}
</style>


